body {
  max-width: 600px;
  flex-direction: column;
  align-items: center;
  margin: 0 auto;
  padding: 20px;
  font-family: Arial, sans-serif;
  display: flex;
}

h1 {
  text-align: center;
  margin-bottom: 20px;
}

select {
  width: 100%;
  border: 1px solid #ccc;
  border-radius: 4px;
  margin-bottom: 10px;
  padding: 8px;
  font-size: 16px;
}

.loader {
  text-align: center;
  margin-bottom: 20px;
  display: none;
}

.error {
  color: red;
  text-align: center;
  margin-bottom: 20px;
  display: none;
}

.cat-info {
  flex-direction: column;
  align-items: flex-start;
  margin-top: 20px;
  display: flex;
}

.cat-info img {
  max-width: 100%;
  height: auto;
  border-radius: 5px;
  margin-right: 20px;
  box-shadow: 0 2px 4px rgba(0, 0, 0, .1);
}

.loaders {
  width: 48px;
  height: 48px;
  box-sizing: border-box;
  border: 5px solid #fff;
  border-bottom-color: #ff3d00;
  border-radius: 50%;
  animation: rotation 1s linear infinite;
  display: inline-block;
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

